import Auth from './Auth.js';
import * as firebase from 'firebase';

const DefaultUser = {
    name: "",
    bank: 100,
    betTarget: "",
    bet: {
        amount: 0,
        payout: 0 
    }
};

class User {

    constructor() {
        this.auth = new Auth();
        this.auth.stateChanged = this.authStateChanged.bind(this);
        this.isLoggedIn = this.auth.isLoggedIn;
        this.name = "";
        this.bank = 0;
        this.bet = {
            amount: 0,
            payout: 0
        };
        this.betTarget = "";
        this.rootRef = firebase.database().ref();
        this.usersRef = this.rootRef.child("users");
        this.userRef = null;
    }

    signIn() {
        this.auth.signIn();
    }

    signOut() {
        this.auth.signOut();
    }

    setIsLoggedIn(isLoggedIn) {
        this.isLoggedIn = isLoggedIn;
        if (this.isLoggedInChanged) this.isLoggedInChanged();
    }

    setName(name) {
        this.name = name;
        if (this.nameChanged) this.nameChanged();
    }

    pushName(name) {
        this.userRef.child("name").set(name);
    }

    setBank(bank) {
        this.bank = bank;
        if (this.bankChanged) this.bankChanged();
    }

    pushBank(bank) {
        this.userRef.child("bank").set(bank);
    }

    setBetTarget(betTarget) {
        this.betTarget = betTarget;
        if (this.betTargetChanged) this.betTargetChanged();
    }

    pushBetTarget(betTarget) {
        this.userRef.child("betTarget").set(betTarget);
    }

    setBetAmount(betAmount) {
        this.bet.amount = betAmount;
        if (this.betAmountChanged) this.betAmountChanged();
    }

    pushBetAmount(betAmount) {
        this.userRef.child("bet").child("amount").set(betAmount);
    }

    setBetPayout(betPayout) {
        this.bet.payout = betPayout;
        if (this.betPayoutChanged) this.betPayoutChanged();
    }

    pushBetPayout(betPayout) {
        this.userRef.child("bet").child("payout").set(betPayout);
    }

    makeBet(amount) {
        console.log(amount);
        if (!this.betTarget) return;
        if (this.bank < amount) return;
        var potRef = this.rootRef.child(this.betTarget).child("pot");
        this.pushBank(this.bank - amount);
        this.pushBetAmount(this.bet.amount + amount);
        potRef.transaction(function(pot) {
            pot += amount;
            return pot;
        });
    }

    clearBets() {
        if (!this.isLoggedIn || !this.userRef) return;
        this.pushBetAmount(0);
        this.pushBetPayout(0);
        this.pushBetTarget(null);
    }

    authStateChanged() {
        this.setIsLoggedIn(this.auth.isLoggedIn);
        if (this.isLoggedIn) {
            this.setupUser();
        } else {
            this.clearUser();
        }
    }

    setupUser() {
        this.usersRef.once("value", snap => {
            if (!snap.hasChild(this.auth.uid)) {
                this.usersRef.child(this.auth.uid).set(DefaultUser);
            }
            this.userRef = this.usersRef.child(this.auth.uid);
            this.userRef.child("name").on("value", snap => {
                this.setName(snap.val());
            });
            this.userRef.child("bank").on("value", snap => {
                this.setBank(snap.val());
            });
            this.userRef.child("betTarget").on("value", snap => {
                this.setBetTarget(snap.val());
            });
            this.userRef.child("bet").child("amount").on("value", snap => {
                this.setBetAmount(snap.val());
            });
            this.userRef.child("bet").child("payout").on("value", snap => {
                this.setBetPayout(snap.val());
            });
        });
    }

    clearUser() {
        this.userRef = null;
        this.setName("");
        this.setBank(0);
        this.setBetTarget("");
        this.setBetAmount(0);
        this.setBetPayout(0);
    }
}

export default User;


