import React, { Component } from 'react';

export class UserStatus extends Component {
    render() {
        if (!this.props.user || !this.props.user.isLoggedIn  || !this.props.user.name) return null;
        return (
            <div className="header">
                <span>{this.props.user.name}</span>
                <span>${this.props.user.bank}</span>
            </div>
        );
    }
}

export class AuthToggle extends Component {
    render() {
        // if (this.props.isLoggedIn) return null;
        var buttonText = this.props.isLoggedIn ? "Sign Out" : "Tubeman Fight!";
        return (
            <div>
                <form onSubmit={this.props.onSubmit}>
                    <button type="submit" className="button" value={buttonText}>{buttonText}</button>
                </form>
            </div>
        );
    }
}

export class SetNameForm extends Component {
    render() {
        return (
            <form onSubmit={this.props.onSubmit}>
                <input type="text" className="textInput" onChange={this.props.onNameChanged} placeholder="Enter name..." />
                <button type="submit" className="button" value="Go">Go</button>
            </form>
        );
    }
}

export class WaitingForFightView extends Component {
    render() {
        if (this.props.isLoggedIn) return null;
        return (
            <div>
                <h2>Waiting For Fight To Start</h2>
            </div>
        );
    }
}

export class MidFightView extends Component {
    render() {
        if (!this.props.user) return null;
        var contentView = ( <MidFightBetView isBetting={this.props.isBetting} betTarget={this.props.betTarget} bet={this.props.user.bet} onBet={this.props.onBet} /> );
        if (this.props.betTarget === null) {
            contentView = ( <TubemanSelectView tubeman1={this.props.tubeman1} tubeman2={this.props.tubeman2} onSetBetTarget={this.props.onSetBetTarget} /> );
        }
        return (
            <div className="content">
                <h2>Fight Started!</h2>
                <div>
                   {contentView}
                </div>
            </div>
        );
    }
}

export class TubemanSelectView extends Component {
    render() {
        return (
            <form>
                <h3>Pick your fighter</h3>
                <button className="button" onClick={this.props.onSetBetTarget} name={this.props.tubeman1.key}>{this.props.tubeman1.name}</button>
                <button className="button" onClick={this.props.onSetBetTarget} name={this.props.tubeman2.key}>{this.props.tubeman2.name}</button>
            </form>
        );
    }
}

export class MidFightBetView extends Component {
    render() {
        if (!this.props.betTarget || !this.props.bet) return null;
        return (
            <div className="split-column">
                <h3>{this.props.betTarget.name} {this.props.betTarget.odds}</h3>
                <div>Your bet: ${this.props.bet.amount}</div>
                <BetForm onBet={this.props.onBet} isBetting={this.props.isBetting} />
            </div>
        );
    }
}

export class BetForm extends Component {
    render() {
        var content = null;
        if (this.props.isBetting)
        {
            content = (
                <form>
                <button className="button" name="5" disabled>Placing bet...</button>
                <button className="button" name="25" disabled >Placing bet...</button>
                </form>
            );
        }
        else
        {
            content = (
                <form>
                <button onClick={this.props.onBet} className="button" name="5">Bet 5$</button>
                <button onClick={this.props.onBet} className="button" name="25">Bet 25$</button>
                </form>
            );
        }
        return (
            <div>{content}</div>
        );
    }
}

export class FightOverView extends Component {
    render() {
        if (!this.props.winner || !this.props.user) return null;
        var youWon = this.props.winner === this.props.yourBet;
        return (
            <div className="content">
                <h1>{this.props.winner.name} won!</h1>
                <div>
                    <FightOverResult tubeman={this.props.yourBet} bet={this.props.user.bet} youWon={youWon} />
                </div>
            </div>
        );
    }
}

class FightOverResult extends Component {
    render() {
        if (!this.props.tubeman || !this.props.bet) return null;
        var winOrLose = this.props.youWon ? "won" : "lost";
        var stakes = this.props.youWon ? this.props.bet.payout - this.props.bet.amount : this.props.bet.amount;
        return (
            <div className="split-column center">
                <p>You bet ${this.props.bet.amount} on {this.props.tubeman.name} at {this.props.tubeman.odds} odds</p>
                <h3>You {winOrLose} ${stakes}</h3>
            </div>
        );
    }
}



