import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import User from './User.js';
import { UserStatus, AuthToggle, SetNameForm, WaitingForFightView, MidFightView, FightOverView } from './Components.js';
import * as firebase from 'firebase';

var AppState = {
    WaitingForFight: 0,
    MidFight: 1,
    FightEnded: 2
}


class App extends Component {

    constructor() {
        super();
        this.toggleSignIn = this.toggleSignIn.bind(this);
        this.nameChanged = this.nameChanged.bind(this);
        this.setBetTarget = this.setBetTarget.bind(this);
        this.finishBet = this.finishBet.bind(this);
        this.bet = this.bet.bind(this);
        this.setName = this.setName.bind(this);
        this.state = {
            isBetting: false,
            tentativeName: "",
            appState: 0,
            winnerKey: "",
            tubeman1: {
                key: "tubeman1",
                name: "green",
                odds: "1/1"
            },
            tubeman2:  {
                key: "tubeman2",
                name: "blue",
                odds: "1/1"
            },
            user: {
                name: "",
                bank: 0,
                isLoggedIn: true,
                betTarget: "",
                bet: {
                    amount: 0,
                    payout: 0
                }
            }
        };
    }

    componentDidMount() {
        this.setupUser();
        this.setupDBRefs();
    }

    setupDBRefs() {
        this.rootRef = firebase.database().ref();
        this.rootRef.child("appState").on("value", snap => {
            if (snap.val() === 0) this.user.clearBets();
            this.setState({appState: snap.val()});
        });
        this.rootRef.child("winnerKey").on("value", snap => {
            this.setState({winnerKey: snap.val()});
        });
        this.rootRef.child("tubeman1").child("key").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman1: {...prevState.tubeman1, key: snap.val()}}
            ));
        });
        this.rootRef.child("tubeman1").child("name").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman1: {...prevState.tubeman1, name: snap.val()}}
            ));
        });
        this.rootRef.child("tubeman1").child("odds").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman1: {...prevState.tubeman1, odds: snap.val()}}
            ));
        });
        this.rootRef.child("tubeman2").child("key").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman2: {...prevState.tubeman2, key: snap.val()}}
            ));
        });
        this.rootRef.child("tubeman2").child("name").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman2: {...prevState.tubeman2, name: snap.val()}}
            ));
        });
        this.rootRef.child("tubeman2").child("odds").on("value", snap => {
            this.setState((prevState, props) => ({
                tubeman2: {...prevState.tubeman2, odds: snap.val()}}
            ));
        });
    }

    setupUser() {
        this.user = new User();
        this.user.isLoggedInChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, isLoggedIn: this.user.isLoggedIn}
        }));
        this.user.nameChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, name: this.user.name}
        }));
        this.user.bankChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, bank: this.user.bank}
        }));
        this.user.betAmountChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, bet: {...prevState.user.bet, amount: this.user.bet.amount}}
        }));
        this.user.betPayoutChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, bet: {...prevState.user.bet, payout: this.user.bet.payout}}
        }));
        this.user.betTargetChanged = () => this.setState((prevState, props) => ({
            user: {...prevState.user, betTarget: this.user.betTarget}
        }));
    }

    toggleSignIn(event) {
        if (this.user.isLoggedIn) this.user.signOut();
        else this.user.signIn();
        event.preventDefault();
    }

    nameChanged(event) {
        this.setState({
            tentativeName: event.target.value
        });
    }

    setName(event) {
        event.preventDefault();
        this.user.pushName(this.state.tentativeName);
    }

    setBetTarget(event) {
        event.preventDefault();
        this.user.pushBetTarget(event.target.name);
    }

    bet(event) {
        event.preventDefault();
        this.setState({ isBetting: true });
        this.betAmount = parseInt(event.target.name, 10);
        setTimeout(this.finishBet, 3000);
        // console.log(event);
        // console.log(event.target);
    }

    finishBet() {
        if (this.state.appState === AppState.MidFight) {
            this.user.makeBet(this.betAmount);
        }
        this.setState({ isBetting: false });
    }

    tubemanForKey(key) {
        if (key === this.state.tubeman1.key) return this.state.tubeman1;
        else if (key === this.state.tubeman2.key) return this.state.tubeman2;
        return null;
    }

    render() {
        var contentView = null;
        if (!this.state.user.isLoggedIn) {
            contentView = null;
        } else if (this.state.user.isLoggedIn && !this.state.user.name) {
            contentView = ( <SetNameForm onNameChanged={this.nameChanged} onSubmit={this.setName} /> );

        } else if (this.state.appState === AppState.WaitingForFight) {
            contentView = ( <WaitingForFightView /> );

        } else if (this.state.appState === AppState.MidFight) {
            var betTarget = this.tubemanForKey(this.user.betTarget);
            contentView = ( <MidFightView
                                betTarget={betTarget}
                                user={this.state.user}
                                tubeman1={this.state.tubeman1}
                                tubeman2={this.state.tubeman2}
                                onSetBetTarget={this.setBetTarget}
                                isBetting={this.state.isBetting}
                                onBet={this.bet} /> );

        } else if (this.state.appState === AppState.FightEnded) {
            var winner = this.tubemanForKey(this.state.winnerKey);
            var yourBet = this.tubemanForKey(this.state.user.betTarget);
            contentView = ( <FightOverView
                                yourBet={yourBet}
                                winner={winner}
                                user={this.state.user} /> );
        }

        return (
            <div className="App">
                <UserStatus user={this.state.user} />
                {contentView}
                <AuthToggle isLoggedIn={this.state.user.isLoggedIn} onSubmit={this.toggleSignIn} />
            </div>
        );
    }
}

export default App;



